@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");
:root {
  --mainC: #800020;
  --subC: #CC0033;
  --mainF: #757575;
  --subF: #9E9E9E;
  --bk: #282828;
  --wh: #fff;
  --fm: "Roboto", sans-serif;
}

* {
  font-family: var(--fm);
  font-size: 14px;
  margin: 0;
  padding: 0;
}

ul, ol, li {
  list-style: none;
}

button {
  outline: none;
  border: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.btn_wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  gap: 0 10px;
}

div {
  max-width: 500px;
}

.com_title {
  text-align: center;
  margin: 20px 0 10px;
  max-width: 500px;
  font-size: 26px;
  color: var(--bk);
  font-weight: 800;
}

.btn_default {
  background-color: var(--subC);
  color: var(--wh);
  border-radius: 5px;
  padding: 5px 10px;
}

.border_box {
  border-radius: 0px;
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid var(--subC);
}

.symbol_wrap {
  border-radius: 30px;
  background-color: var(--subC);
  color: var(--wh);
  font-weight: 700;
  padding: 2px 5px;
  font-size: 10px;
  letter-spacing: 1px;
  text-align: center;
}

.input_styles {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin: 5px;
}

.title_hover {
  font-size: 24px;
  padding: 5px;
  color: var(--bk);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px 0;
}

.title_hover span:hover {
  color: var(--mainC);
  border-bottom: 1px solid var(--subF);
}

.box {
  margin-bottom: 40px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--subF);
}

.list_item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  gap: 0 10px;
  padding: 10px 0;
}
.list_item:not(:last-child) {
  border-bottom: 1px solid var(--subF);
}
.list_item b {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.list_item b::before {
  content: "ðŸ’°";
  width: 20px;
  height: 20px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.detail_wrap {
  max-width: 800px;
  border-radius: 5px;
  background: rgba(241, 241, 241, 0.3);
  padding: 20px;
}
.detail_wrap .detail_img {
  margin: 20px 0;
  text-align: center;
}
.detail_wrap * {
  word-break: break-word;
}

.detail_title {
  font-size: 26px;
  color: var(--mainC);
  margin: 20px;
}

.detail_info {
  color: var(--mainF);
  line-height: 150%;
  margin-bottom: 30px;
  letter-spacing: -0.3px;
}

.detail_genres {
  font-size: 12px;
  font-weight: 100;
  line-height: 200%;
  color: var(--subF);
}
.detail_genres::before {
  content: "ðŸŽ¬";
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.detail_date {
  font-size: 12px;
  color: var(--subF);
  margin: 10px 0;
}